   </CButton>\n        </form>\n    </div>\n</template>\n<script>\n//\nexport default {\n    name: \"CompanyBankForm\",\n    components: {},\n    props: {\n        id: null\n    },\n    data() {\n        return {\n            formData: {},\n            users: {\n                items: [],\n                loading: false\n            },\n            loading: false\n        };\n    },\n    watch: {\n        users: [\n            function search(val) {\n                let self = this;\n                if (self.users.length > 0 || self.users.loading) return;\n                self.users.loading = true;\n                this.$store\n                    .dispatch(\"user/get\", {})\n                    .then(response => {\n                        self.users.items = response.data;\n                        self.users.loading = false;\n                    })\n                    .catch(error => {\n                        self.loading = false;\n                    });\n            }\n        ]\n    },\n    mounted() {\n        this.fetch();\n    },\n    methods: {\n        fetch() {\n            let self = this;\n            let data = {\n                id: self.$route.params.id\n            };\n            this.$store\n                .dispatch(\"company/bankaccount/details\", data)\n                .then(response => {\n                    self.formData = response.data.data;\n                })\n                .catch(error => {});\n        },\n        update() {\n            let self = this;\n            self.loading = true;\n            let data = {\n                id: self.$route.params.id,\n                owner: self.formData.owner,\n                bank: self.formData.bank,\n                account_type: self.formData.account_type\n            };\n            this.$store\n                .dispatch(\"company/bankaccount/update\", data)\n                .then(response => {\n                    self.loading = false;\n                })\n                .catch(error => {\n                    self.loading = false;\n                });\n        }\n    }\n};\n</script>\n\n<style scoped>\n.v-text-field >>> input {\n    font-size: 0.8em;\n    font-weight: 100;\n}\n.v-text-field >>> label {\n    font-size: 0.8em;\n}\n.v-text-field >>> button {\n    font-size: 0.8em;\n}\n</style>\n"],"sourceRoot":""}]);
