<template>
    <div>
        <form v-on:submit.prevent>
            <v-autocomplete
                v-if="$store.getters.isAdmin"
                v-model="formData.owner"
                :items="users.items"
                :loading="users.loading"
                :search-input.sync="users.search"
                required
                outlined
                dense
                hide-no-data
                hide-selected
                item-text="name"
                item-value="id"
                label="Owner"
                return-object
                :disabled="!$store.getters.isAdmin"
            ></v-autocomplete>
            <v-text-field
                v-model="formData.bank"
                label="Bank"
                required
                outlined
                dense
                clearable
                :disabled="!$store.getters.isAdmin"
            ></v-text-field>
            <v-text-field
                v-model="formData.account_type"
                label="Bank Account Type"
                required
                outlined
                dense
                clearable
                :disabled="!$store.getters.isAdmin"
            ></v-text-field>
            <CButton @click="update" color="primary" class="px-4">
                Update
                <v-progress-circular
                    v-if="loading"
                    indeterminate
                    color="primary"
                    :size="15"
                ></v-progress-circular>
            </CButton>
        </form>
    </div>
</template>
<script>
//
export default {
    name: "CompanyBankForm",
    components: {},
    props: {
        id: null
    },
    data() {
        return {
            formData: {},
            users: {
                items: [],
                loading: false
            },
            loading: false
        };
    },
    watch: {
        users: [
            function search(val) {
                let self = this;
                if (self.users.length > 0 || self.users.loading) return;
                self.users.loading = true;
                this.$store
                    .dispatch("user/get", {})
                    .then(response => {
                        self.users.items = response.data;
                        self.users.loading = false;
                    })
                    .catch(error => {
                        self.loading = false;
                    });
            }
        ]
    },
    mounted() {
        this.fetch();
    },
    methods: {
        fetch() {
            let self = this;
            let data = {
                id: self.$route.params.id
            };
            this.$store
                .dispatch("company/bankaccount/details", data)
                .then(response => {
                    self.formData = response.data.data;
                })
                .catch(error => {});
        },
        update() {
            let self = this;
            self.loading = true;
            let data = {
                id: self.$route.params.id,
                owner: self.formData.owner,
                bank: self.formData.bank,
                account_type: self.formData.account_type
            };
            this.$store
                .dispatch("company/bankaccount/update", data)
                .then(response => {
                    self.loading = false;
                })
                .catch(error => {
                    self.loading = false;
                });
        }
    }
};
</script>

<style scoped>
.v-text-field >>> input {
    font-size: 0.8em;
    font-weight: 100;
}
.v-text-field >>> label {
    font-size: 0.8em;
}
.v-text-field >>> button {
    font-size: 0.8em;
}
</style>
